.footer{
background-color: $color-silhouette-dark;
color:$color-silhouette-light;
font-size: $x-small;
padding: 30px 30px;

& hr {
border-top: 1px solid #555;
margin: 30px 0;
}

& h5 {
font-size: $med;
font-family: inherit;
margin-top: 10px;
}

& h5, & a {
color:$color-silhouette-yellow !important;
}

& .icons a {
font-size: $med;
margin: 0 8px;
}

}//end footer
